body {
  position: relative;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  color: #222;
  overflow-x: hidden;
  min-height: 100vh;
  /* Clase para bloquear el scroll cuando el modal está abierto */
}
body.body-scroll-lock {
  overflow: hidden;
  position: fixed;
  width: 100%;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  font-weight: 100;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.botones {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.botones.botones-datos {
  justify-content: space-evenly;
}
.botones.botones-resumen {
  justify-content: space-between;
}

.boton {
  margin: 4rem 0 0 0;
  outline: none;
  text-decoration: none;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  height: 60px;
  width: fit-content;
  padding: 1rem;
  cursor: pointer;
  background-color: #333;
  border: 1px solid #262626;
  color: #fff;
}
.boton.boton-agregar-carrito, .boton.boton-siguiente, .boton.boton-anterior {
  background-color: #222;
}
.boton.boton-agregar-carrito a, .boton.boton-siguiente a, .boton.boton-anterior a {
  color: #fff;
}
.boton.boton-siguiente, .boton.boton-anterior {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.boton.boton-siguiente[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}
.boton.boton-pagar {
  width: fit-content;
  margin: 1rem 0 0 0;
  padding: 0 2rem;
  background-color: transparent;
}
.boton.boton-pagar a {
  color: #222;
}
.boton a {
  text-decoration: none;
  color: #333;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.2rem;
}
.boton:hover {
  animation: rotate 0.7s ease-in-out both;
}
.boton:hover a {
  animation: storm 0.7s ease-in-out both;
  animation-delay: 0.06s;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}
@keyframes storm {
  0% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
  25% {
    transform: translate3d(4px, 0, 0) translateZ(0);
  }
  50% {
    transform: translate3d(-3px, 0, 0) translateZ(0);
  }
  75% {
    transform: translate3d(2px, 0, 0) translateZ(0);
  }
  100% {
    transform: translate3d(0, 0, 0) translateZ(0);
  }
}
header {
  position: relative;
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 30%;
  height: 100%;
  background-color: #e0ddcf;
  border-bottom-left-radius: 15rem;
  border-bottom-right-radius: 15rem;
  z-index: -1;
}
header .titulo {
  width: 60%;
  height: 65%;
  place-self: center;
  place-content: center;
}
header .titulo h2, header .titulo h1, header .titulo p {
  margin: 0;
  padding: 0;
}
header .titulo h2 {
  font-weight: 500;
  font-size: 1.7rem;
}
header .titulo h1 {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 7rem;
  letter-spacing: 1.8rem;
  color: #111;
}
header .titulo p {
  font-weight: 200;
  font-size: 1.6rem;
}
header .imagen-titulo {
  position: absolute;
  top: 0;
  left: 45%;
  bottom: 0;
  width: fit-content;
  height: fit-content;
  place-content: center;
  place-self: center;
  z-index: -1;
}
header .imagen-titulo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
}
header .imagen-titulo img {
  max-width: 70rem;
}
header .marcas {
  display: flex;
  gap: 10rem;
  width: 60%;
}
header .marcas a {
  font-weight: 500;
  font-size: 1.7rem;
  letter-spacing: 0.2rem;
  text-decoration: none;
  color: #222;
}

